import React, { useEffect, useState, useRef } from "react";

import { getStore,  gen16CodeId, ObjectKeys} from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { InvoiceItem_Cmpt, AddInvoice_Cmpt } from "..";
import {  getall_invoice, search_invoice } from "../services/server_fetching";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";



const SearchInput = SearchInput_Cmpt();


const InvoiceItem = InvoiceItem_Cmpt();

const MakeSale = AddInvoice_Cmpt();





const InvoiceDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			getall_invoice(cb_result);
			get_balances_by_type("global_accounts","global_account_params",updObs);
		}
	});


	const openUpdateView =  () => {
		
	}

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}

	const handle_search_invoice =  (v) => {
		set_loading(true)
		search_invoice(cb_result)
	}


	const handle_getall_invoice =  (v) => {
		getall_invoice(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};




	const openSaleView =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<MakeSale />
		
		OpenModal(dataModal);
	}

	

	return (

	<div className="">
		<div className="accounts_header">
			<h2>Invoices</h2>
			<div className="_dsplFlx ">
				<p className="group_title addBtn purple" onClick={openSaleView} >
					Add Invoice
				</p>
				
			</div>
			<div className="_dsplFlx " style={{marginTop:14}} >
				<SearchInput
					placeholder={"Buscar ...."} 
					width={360}
					obs={set_obs}
					fetchData={handle_search_invoice}
					loading={loading}
					change={handleChange}
					callempty={handle_getall_invoice}
					keyFlds={"searchQry"}
				/>
				<div className="flexSpace"/>
				<div className="_dsplFlx ">
					<div className="total_comprobante_title">
						No de Invoices: 
					</div>
					<div className="total_comprobante gray">
						<span>
							{ObjectKeys(getStore("invoices_list"))?.length}
						</span>
					</div>
					
				</div>
			</div>
		</div>
		

		<div className="invoices_container scollVh">
			{getStore("invoices_list") && ObjectKeys(getStore("invoices_list"))?.map(cid=>{
				return (
					<InvoiceItem 
						data={getStore("invoices_list")[cid]} 
						elmId={cid} 
						key={cid}
						openUpdateView={openUpdateView}
						handleRefreshAll={handle_getall_invoice}
					/>
				)
			})}
		</div>
		</div>
	);
};

export default InvoiceDashboard;
