import { fetchEncryptStream, updStore, getStore, convertArray2Obj, ObjectKeys, callbackOnMemory, convertObj2Array, qryStore } from "../../../stores/Utils";




export const getall_invoice = async (cb) => {
    let bdyq2 = {...getStore(qryStore())?.all_invoices};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("invoices_list", sortInvoices(td,"date"));
        updStore("invoices_ids", convertArray2Obj(convertObj2Array(td),"invoiceId"));
        cb && cb();
    }
};




export const search_invoice = async (cb) => {

    let bdyq2 = {...getStore(qryStore())?.search_invoice};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":search": getStore("searchQry")
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("invoices_list", sortInvoices(convertObj2Array(td),"date"));
        cb && cb();
    }
};





  
export const sortInvoices = (td, key) =>
callbackOnMemory(
	"sortInvoices",
	[td],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key];
			let B = b[key];
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);






export const delete_invoice = async (invoiceId, cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.delete_invoice};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        invoiceId: invoiceId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        cb && cb();
    }
};




